.wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
  pointer-events: none;
}

.button {
  background-color: var(--primary-1);
  height: 24px;
  line-height: 22px;
  width: 40px;
  position: absolute;
  top: 0;
  left: calc(50% - 20px);
  text-align: center;
  transition: all 0.3s ease-out;
  user-select: none;
  font-weight: 500;
  color: var(--text-1);
  pointer-events: 'auto';
  border-radius: 0 0 5px 5px;
  overflow: hidden;
  font-family: 'Menlo, Monaco, "Courier New", monospace';
  box-shadow: 0 0 2px var(--bg-1);
  cursor: pointer;

  &:hover {
    background-color: var(--primary-2);
  }

  &:active {
    background-color: var(--primary-1);
  }

  &.active {
    width: 100%;
    left: 0;
    border-radius: 0;
  }
}